<template>
    <div class="edit-modal">
        <div class="modal">
            <i class="fa-solid fa-xmark close-modal" @click="closeEdit"></i>
            <h2 class="modal-heading">Edit User</h2>
            <span>Edit the user email address.</span>
            <form @submit.prevent="updateUser">
                <div class="form-group">
                    <label for="user_email">Email Address</label>
                    <input 
                      type="email" 
                      class="form-control" 
                      v-model="userInfo.email"
                    >
                </div>
                <div class="form-group">
                    <button class="btn btn-primary w-100">Update</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import usersColRef from '@/firebase'
import { getDoc, doc, setDoc } from 'firebase/firestore'

export default {
    name: 'EditUser',
    data() {
        return {
          selectedUser: {},
          id: null,
          docRef: null,
          userInfo: {
            email: null
          }
        }
    },
    methods: {
      async getUser () {
        const userRef = doc(usersColRef, this.id)
        this.docRef = userRef
        const user = await getDoc(this.docRef)
        const userData = user.data()
        this.userInfo.email = userData.email
      },
      async updateUser () {
        await setDoc(this.docRef, this.userInfo)
        this.$router.push('/users')
      },
      async closeEdit () {
          this.$router.push('/users')
      }
    },
    created () {
      const id = this.$route.params.id
      this.id = id
      this.getUser()
    }
}
</script>

<style scoped>
.edit-modal {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(255,255,255,.3);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 400;
}
.modal {
  padding: 3rem;
  background: var(--base-color);
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0,0,0,.1);
  position: relative;
  min-width: 650px;
}
.modal-heading {
  color: var(--primary-color);
  font-size: var(--heading-font);
  font-weight: 600;
}
.close-modal {
  font-size: 20px;
  color: var(--primary-color);
  position: absolute;
  right: 3rem;
  top: 2rem;
  cursor: pointer;
  transition: .5s ease all;
}
.close-modal:hover {
  color: var(--secondary-color);
}

.form-group {
    width: 100%;
    display: block;
    margin-bottom: 1rem;
    margin-top: 1.5rem;
}
.form-group label {
    font-size: var(--default-font);
    font-weight: 500;
    color: var(--primary-color);
    display: block;
}
.form-control {
  border: 1px solid var(--input-border-color);
  background: #FAFAFA;
  color: var(--secondary-color);
  padding: 10px 25px;
  border-radius: 10px;
  font-size: var(--default-font);
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  width: 100%;
  box-sizing: border-box;
  transition: .5s ease all;
  outline: 1px solid transparent;
}
.form-control:focus {
  outline: 1px solid var(--primary-color);
}
::placeholder {
  color: #666;
}
.btn {
  padding: 10px 25px;
  border-radius: 10px;
  border: none;
  font-family: 'Poppins', sans-serif;
  font-size: var(--default-font);
  font-weight: 500;
  color: var(--base-color);
  cursor: pointer;
  box-shadow: 0 5px 15px rgba(255,51,0,.3);
  transition: .5s ease all;
}
.btn-primary {
  background: var(--primary-color);
}
.btn-primary:hover {
  background: #df3004;
}
.w-100 {
  width: 100%;
  box-sizing: border-box;
}
</style>